.adjust-partner {
  max-width: 100%;
  max-height: 100%;
}

.stores-container-partner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.partner-logo-partner {
  background-size: cover;
  background-position: center;
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

@media (max-width: 600px) {
  .partner-logo-partner {
    width: 80px;
    height: 80px;
  }
}
