.left-container {
  width: fit-content;
  display: flex;
  flex-direction: column;
}

.left-total {
  width: 130px;
  height: 30px;
}

.left-total-2 {
  margin-top: 10px;
  width: 200px;
  height: 20px;
}

.right-side {
  width: 30px;
  height: 20px;
  margin: auto 0;
}

@media (max-width: 500px) {
  .left-total {
    width: 100px;
  }
  .left-total-2 {
    width: 150px;
  }
  .chart-title-loader {
    width: 100px !important;
    height: 30px !important;
  }
}

.chart-title-loader {
  width: 200px;
  height: 50px;
}

.chart-dd-loader {
  width: 70px;
  height: 30px;
}

.graph-store-name-loader {
  margin-top: 10px;
  width: 100px;
  height: 20px;
}

.graph-loader {
  width: 100%;
  height: 300px;
}

.visit-details-name {
  width: 100px;
  height: 20px;
}

.visit-details-inc-dec {
  width: 200px;
  height: 20px;
}

.visit-details-count-loader {
  width: 20px;
  height: 20px;
}

.progress-bar-loader {
  width: 100%;
  height: 20px;
}
