@media (max-width: 600px) {
  .mobile-heading {
    margin: 0 !important;
    padding: 0;
    font-size: 20px;
  }
  .mobile-heading h4 {
    font-size: 15px;
    margin: 0;
  }
  .heading-number h2 {
    max-width: 250px !important;
    max-height: 100px !important;
    height: 100%;
  }
  .partner-header {
    margin-top: -5px !important;
  }
}

.heading-steps {
  min-width: 50%;
}

.heading-number {
  max-width: 800px;
  min-width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.heading-number h2 {
  width: 100%;
  max-width: 800px;
  height: 100%;
  /* min-height: fit-content; */
  max-height: 500px;
}

.partner-header {
  border: 3px solid #ee6d32;
  padding: 10px;
  transition: all 0.2s ease-out;
  cursor: pointer;
  margin-top: 10px;
}

.status-indigator {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1px solid transparent;
}

@media (max-width: 500px) {
  .status-indigator-icon {
    width: 20px !important;
    height: 20px !important;
    top: 10px !important;
    right: 10px !important;
  }
}

.status-indigator-icon {
  position: absolute;
  top: 13px;
  right: 17px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid transparent;
}
.tooltip-icon {
  width: 100%;
  height: 100%;
}

.shop-open {
  /* background: linear-gradient(#8aac5a, #89ac5ab6); */
  border: 4px solid #a0d05d;
  animation: glow 1.5s ease infinite;
}

@keyframes glow {
  0% {
    box-shadow: 0 0 5px 0 #53ec53;
  }
  100% {
    box-shadow: 0 0 10px 8px transparent;
  }
}

.shop-close {
  background: linear-gradient(#fe5593, #fe5559ba);
}
.dummy-guy {
  height: 50%;
}
