.each-store {
  background: linear-gradient(#fffffd, #fffffd);
}

.stores-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.progress-bar {
  border-radius: 9px;
  margin: 20px auto;
  background: rgb(213, 213, 213) !important;
}

.progress-bars-container {
  padding: 10px;
  border-radius: 20px;
  width: 100%;
}
.progress-bar-container {
  font-weight: bold;
  color: rgb(84, 84, 84);
}

.progress-bar-container {
  width: 100%;
  height: fit-content;
}

.each-progress-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.inc-dec-detail {
  font-weight: 500;
}

.dashboard-details-container {
  border-radius: 20px;
  box-shadow: 9px 9px 16px rgba(198, 163, 166, 0.6),
    -9px -9px 16px rgba(235, 219, 219, 0.5);
}

.dropdown-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row-reverse;
}

.dropdown-container button {
  color: white !important;
  box-shadow: none;
}

.dropdown-container button:focus {
  box-shadow: none !important;
}

.dropdown-container .dropdown-menu {
  margin-left: -10px;
  max-height: 200px;
  overflow-y: auto;
}

.dropdown-component > p {
  margin: 0;
  font-size: small;
}

.dropdown-component > small {
  margin: 0;
  font-size: xx-small;
}

.dropdown-component-label > small {
  margin: 0;
  font-size: small;
}

.graph-loading-container {
  position: absolute;
  top: 2px;
  right: 0px;
  width: fit-content;
  padding-right: 20px;
  height: fit-content;
  /* overflow: hidden; */
}
