.navbar {
  position: fixed;
  z-index: 1000;
  /* height: 100%; */
  width: 95vw;
  /* float: left; */
  left: 50%;
  transform: translateX(-50%);
  padding: 10px 20px;
  /* background: #ee3532cf; */
  /* background: #6e0009; */
  border-radius: 10px;
  color: #fff;
  font-family: "Poppins", sans-serif;
  transition: all 0.3s ease-out;
}
