.image {
  width: 50px;
  height: 50px;
  border-radius: 20px;
  transition: all 0.2s ease-in;
  border: 5px dashed transparent;
}

.skeleton-loading {
  -webkit-animation: skeleton-loading 1.5s infinite;
          animation: skeleton-loading 1.5s infinite;
}

@-webkit-keyframes skeleton-loading {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes skeleton-loading {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.dotted img {
  transition: all 0.2s ease-in;
  border: 5px dashed grey;
  border-radius: 20px;
  transform: rotateZ(1deg);
}

.my-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100000;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.626);
  transition: transform 0.5s ease-in-out;
}

.outer-modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent !important;
  z-index: 100001;
}

.modal-content {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border: none;
  outline: none;
  z-index: 100002;
}

